import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { CompanyService } from '../../../services/company.service'
import { CompanyList } from '../list/list.component'

@Component({
  selector: 'ngx-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  company:CompanyList=new CompanyList()

  constructor(private companyService:CompanyService,
              private route:ActivatedRoute,
              private router:Router) { }

  ngOnInit() {
    this.route.queryParams.subscribe(params=>{
      if(params['id']){
        this.companyService.getOne(params['id']).subscribe((res:any)=>{
          if(res && res.isSuccessful){
            this.company=res.data;
          }
        })
      }
    })
  }

  public save() {
    this.companyService.edit(this.company).subscribe((res:any)=>{
      if(res){
        this.router.navigateByUrl('pages/company')
      }
    })
  }

  public imageUpload(event) {
    if(event.target.files[0]&& event.target.files[0].size<=2048000){
      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      let self=this;
      reader.onload = () => {
        self.company.avatar=reader.result.toString();
      };
    }
  }
}
