import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { CompanyService } from '../../../services/company.service'

@Component({
  selector: 'ngx-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
  page:number=0;
  filter:string='';
  count:number=0;
  companyList:Array<CompanyList>=[];

  constructor(private companyService:CompanyService,
              private router:Router) { }

  ngOnInit() {
    this.getCompanyPagination(this.page,this.filter)
  }

  changePage(event) {
    this.page = event;
    this.getCompanyPagination(event,this.filter)
  }

  public pageChanged(event: number) {
    this.page = event;
    this.getCompanyPagination(event,this.filter)
  }

  public getCompanyPagination(page: number,filter:string) {
    this.companyService.getAll(page,filter).subscribe((res:any)=>{
      this.count=res.totalDocs;
      this.companyList=res.docs;
    })
  }

  public edit(_id: string) {
    this.router.navigate(['pages/company/add'],{queryParams:{id:_id}})
  }
}
export class CompanyList {
  description: string;
  avatar: string;
  deleted: boolean
  created_at: any
  updated_at: any
  _id: string;
  CoID: number
  CoName:string;
  CoNameEnglish:string;
  CompanySymbol:string;
  CoTSESymbol:string;
  GroupID: number
  GroupName:string;
  IndustryID: number
  IndustryName:string;
  InstCode:string;
  TseCIsinCode:string;
  TseSIsinCode:string;
  MarketID: number
  MarketName:string;
}
